.compare-bikes {
  &__products {
    @include page-grid;

    row-gap: 40px;
    justify-items: center;

    @include on-tablet {
      row-gap: 48px;
    }
  }

  &__product {
    grid-column: 1 / -1;

    @include on-tablet {
      grid-column: 2 / 6;
    }

    @include on-desktop {
      grid-column: 4 span;
    }
  }
}
